import router from '@/router';

export default {
    name: 'userCategoryAdd',
    data() {
        return {
            ruleForm: {
                skuType: '',
                infoType: '',
                matchType: '',
                partskuName: '',
                feedback: '',
                limit: '',
                required:'',
                sort:''
            },
            rules: {
                skuType: [
                    { required: true, message: '请填写属性所属的范围' }
                ],
                infoType: [
                    { required: true, message: '请选择信息类型' }
                ],
                matchType: [
                    { required: true, message: '请选择匹配类型' }
                ],
                partskuName: [
                    { required: true, message: '请输入属性名称' }
                ],
                feedback: [
                    { required: true, message: '请输入反馈文案' }
                ],
                limit: [
                    { required: true, message: '请输入属性限制' },
                    { type: 'integer', message: '属性限制必须为整数'}
                ],
                required: [
                    { required: true, message: '请输入必填选项' }
                ],
                sort: [
                    { required: true, message: '请输入属性排序' },
                    { type: 'integer', message: '属性排序必须为整数'}
                ]

            },
            scopeOptions: [{
                value: '0',
                label: '产品属性'
            }, {
                value: '1',
                label: '编码属性'
            }],
            infoTypeOptions: [{
                value: '0',
                label: '图片'
            }, {
                value: '1',
                label: '文字'
            }, {
                value: '2',
                label: 'JSON（组数据，暂不启用）'
            }],
            matchTypeOptions: [{
                value: '0',
                label: '普通信息'
            }, {
                value: '1',
                label: '编码Code信息'
            }],
            requiredOptions: [{
                value: '0',
                label: '是'
            }, {
                value: '1',
                label: '否'
            }],

        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                    this.routeTo('catgPartSkuEdit');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        // 跳转页面
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



