<template>
    <div v-title data-title="运营管理-品类配置-创建属性">
        <div class="catgPartskuAdd p-30">
            <!-- breadcrumb -->
            <el-breadcrumb separator="/">
                <i class="iconfont icon-home fl m-r-10"></i>
                <el-breadcrumb-item :to="{ path: '/catgConfig' }">品类配置</el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: '/catgPartSkuEdit' }">属性编辑</el-breadcrumb-item>
                <el-breadcrumb-item>创建属性</el-breadcrumb-item>
            </el-breadcrumb>
            <!-- form  -->
            <el-row>
                <el-col :span="20" :offset="2">
                    <div class="title text-center m-b-25">创建属性</div>

                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">

                        <el-form-item label="属性类型" prop="scopeof" required>
                            <el-input type="text" placeholder="空为普通信息，添加则表示为编码code信息，系统自动添加code_为前缀，限定为字母数字" v-model="ruleForm.skuType"></el-input>
                        </el-form-item>

                        <el-form-item label="信息类型" prop="infoType" required>
                            <el-radio-group v-model="ruleForm.infoType">
                                <el-radio v-for="item in infoTypeOptions" :key="item.value" :label="item.label" :disabled="item.value == 2">{{item.label}}</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="匹配类型" prop="matchType" required>
                            <el-radio-group v-model="ruleForm.matchType">
                                <el-radio v-for="item in matchTypeOptions" :key="item.value" :label="item.label">{{item.label}}</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="属性名称" prop="partskuName" required>
                             <el-input type="text" v-model="ruleForm.partskuName"></el-input>
                        </el-form-item>

                        <el-form-item label="反馈文案" prop="feedback" required>
                             <el-input type="text" v-model="ruleForm.feedback"></el-input>
                        </el-form-item>

                        <el-form-item label="属性限制" prop="limit" required v-if="ruleForm.infoType">
                            <div v-if="ruleForm.infoType == '文字'">
                                <span class="m-r-10">文字最长</span><el-input class="limit-width" type="number" v-model.number="ruleForm.limit"></el-input><span class="m-l-10">字符</span>
                            </div>
                            <div v-else>
                                <span class="m-r-10">图片最多</span><el-input class="limit-width" type="number" v-model.number="ruleForm.limit"></el-input><span class="m-l-10">张</span>
                            </div>
                        </el-form-item>

                        <el-form-item label="必填选项" prop="required" required>
                            <el-radio-group v-model="ruleForm.required">
                                <el-radio v-for="item in requiredOptions" :key="item.value" :label="item.label">{{item.label}}</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="属性排序" prop="sort" required>
                             <el-input type="number" placeholder="填写数字号码表示排位顺序" v-model.number="ruleForm.sort"></el-input>
                        </el-form-item>

                        <el-form-item class="text-center">
                            <el-row :gutter="20">
                                <el-col :span="4" :offset="6">
                                    <el-button class="input-width" type="primary" @click="submitForm('ruleForm')"> 创建属性 </el-button>
                                </el-col>
                                <el-col :span="4">
                                    <el-button class="input-width" @click="resetForm('ruleForm')">重置</el-button>
                                </el-col>
                                <el-col :span="4">
                                    <el-button class="input-width" onclick="window.history.go(-1);">返回</el-button>
                                </el-col>
                            </el-row>
                        </el-form-item>
                    </el-form>

                </el-col>
            </el-row>

        </div>
    </div>
</template>

<script>
import pageSet from '@/components/main/js/catgPartskuAdd';

export default {
    name: 'catgPartskuAdd',
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";
.title {
    font-size: 24px;
    font-weight: bold;
}
.input-width{width: 100%;}
.limit-width{width: auto;}
</style>




