<template>
    <div v-title data-title="运营管理-品类配置-属性编辑">
        <div class="catgAdd p-30">
            <!-- breadcrumb -->
            <el-breadcrumb separator="/">
                <i class="iconfont icon-home fl m-r-10"></i>
                <el-breadcrumb-item :to="{ path: '/catgConfig' }">品类配置</el-breadcrumb-item>
                <el-breadcrumb-item>属性编辑</el-breadcrumb-item>
            </el-breadcrumb>
            <!-- form  -->
            <el-row :gutter="20" class="m-t-25">

                <el-col class="list-box" :span="7">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix title">
                            <strong>产品</strong> <span class="f12 gray fr"><span>checkbox</span>（产品启用禁用），<span>编辑</span>（产品属性编辑）</span>
                        </div>

                        <ul>
                            <li v-for="(part, index) in partsList" :key="part.category_cmpro_id">
                                <el-button class="fr" :type="partIndex == index ? 'danger' : 'primary'" size="mini" icon="edit" @click="partEditFn(part, index)" :disabled="part.category_cmpro_required == 0">编辑</el-button>
                                <el-checkbox v-model="part.category_cmpro_required" :label="part.carmodel_pro_name" :true-label='1' :false-label='0' @change="changePartFn(part, index)"></el-checkbox>
                            </li>
                        </ul>
                    </el-card>
                </el-col>

                <el-col class="list-box" :span="17" v-if="partIndex != -1">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix title">
                            <strong>查询的属性</strong>
                            <el-button type="primary" class="fr" @click="routeTo('catgPartskuAdd')">创建属性</el-button>
                        </div>

                            <div class="sub-title">{{partsList[partIndex].carmodel_pro_name}}</div>

                            <div v-if="partsList[partIndex].category_cmpro_required != 0">
                                <div v-for="(part, index) in partsList" :key="part" v-if="partIndex == index">
                                    <draggable element="ul" class="card-list" @end="onEnd" v-model="part.list">
                                        <transition-group class="list-group" type="transition" name="flip-list" tag="ul">
                                            <li class="list-group-item clearfix" v-for="row in partsList[partIndex].list" :key="row">
                                                <el-button class="fr" size="mini" icon="edit" @click="skuEditFn(row)"></el-button>
                                                <el-tag class="m-r-5" type="gray">{{row.category_cmpro_index}}</el-tag>
                                                <el-checkbox v-model="row.category_cmpro_required" :label="row.carmodel_pro_name" :true-label='1' :false-label='0'></el-checkbox>
                                            </li>
                                        </transition-group>
                                    </draggable>
                                </div>
                            </div>

                            <div class="m-t-20 blue text-center" v-else>
                                <i class="el-icon-information f22 vm"></i> <span class="vm">启用产品后才能添加或编辑属性</span>
                            </div>

                    </el-card>
                </el-col>

            </el-row>

            <div class="text-center m-t-35">
                <el-row :gutter="20">
                    <el-col :span="2" :offset="11">
                        <el-button class="input-width" type="primary" @click="routeTo('catgConfig')"> 保存 </el-button>
                    </el-col>
                </el-row>
            </div>

        </div>
    </div>
</template>

<script>
import pageSet from '@/components/main/js/catgPartSkuEdit';

export default {
    name: 'catgPartSkuEdit',
    components: pageSet.components,
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";

.title{
    line-height: 36px;
}

.input-width {
    width: 100%;
}

.sub-title {
    font-weight: bold;
}

.icon-arrow-up{
padding: 3px;
}

.box-card{
    min-height: 400px;
}

.list-box {
    line-height: 35px;
}

.ghost {
    opacity: .5;
    background: $color-D;
}

.flip-list-move {
    transition: transform 0.5s;
}

.list-group-item {
    padding: 0 10px;
    cursor: move;
}

.list-group-item i {
    cursor: pointer;
}
</style>



