import router from '@/router';
import draggable from 'vuedraggable';
// category_cmpro_id: 121066838118136832,//属性id
// carmodel_pro_name: '品牌',//属性名称
// category_cmpro_index: 1,//顺序
// category_cmpro_required: 1//必要标识(0:选填 1: 必填)

/**
 * 初始化对应车型属性待选列表
 */
const category_cmpros = [{
    category_cmpro_id: 121066838118136832,
    carmodel_pro_name: '品牌'
}, {
    category_cmpro_id: 121066838118136833,
    carmodel_pro_name: '厂家'
}, {
    category_cmpro_id: 121066838118136834,
    carmodel_pro_name: '年款'
}, {
    category_cmpro_id: 121066838118136835,
    carmodel_pro_name: '排量'
}, {
    category_cmpro_id: 121066838118136836,
    carmodel_pro_name: '车型名'
}];

const category_cmpros_res = category_cmpros.map((val, index) => {
    return {
        ...val,
        category_cmpro_index: index + 1,
        category_cmpro_required: 0
    };
});

export default {
    name: 'catgAdd',
    components: {
        draggable
    },
    data() {
        return {
            ruleForm: {
                catgname: '',                          //品类名称
                catgcode: '',                          //品类编码
                queryCategory: '',                     //查询类别
                queryCondition: '',                    //查询可选条件
                initiateState: '1',                    //启用状态
                category_cmpros: category_cmpros_res,  //待选列表
                category_cmpros_add: [],               //已选列表
                parts: [{                              //新增产品
                    name: '',
                    code: '',
                    serial: '',
                    key: Date.now()
                }]
            },
            // 表单验证
            rules: {
                catgname: [
                    { required: true, message: '品类名称不能为空' }
                ],
                catgcode: [
                    { required: true, message: '品类编码不能为空' }
                ],
                queryCategory: [
                    { required: true, message: '查询类别不能为空' }
                ],
                queryCondition: [
                    { required: true, message: '查询可选条件不能为空' }
                ],
                initiateState: [
                    { required: true, message: '启用状态不能为空' }
                ],
                category_cmpros_add: [
                    { required: true, message: '对应车型属性不能为空' }
                ]
            }
        };
    },
    computed: {
        dragOptions() {
            return {
                animation: 0,
                group: 'card-list',
                ghostClass: 'ghost'
            };
        }
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        onEnd() {
            function mapFn(d) {
                d.map((val, index) => {
                    val.category_cmpro_index = index + 1;
                    return val;
                });
            }
            mapFn(this.ruleForm.category_cmpros);
            mapFn(this.ruleForm.category_cmpros_add);
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.routeTo('catgPartSkuEdit');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        removePart(item) {
            if (this.ruleForm.part.length == 1) return;
            var index = this.ruleForm.part.indexOf(item);
            if (index !== -1) {
                this.ruleForm.part.splice(index, 1);
            }
        },
        addPart() {
            this.ruleForm.parts.push({
                name: '',
                code: '',
                serial: '',
                key: Date.now()
            });
        },

        // 跳转页面
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



