<template>
    <div v-title data-title="运营管理-品类配置-创建品类">
        <div class="catgAdd p-30">
            <!-- breadcrumb -->
            <el-breadcrumb separator="/">
                <i class="iconfont icon-home fl m-r-10"></i>
                <el-breadcrumb-item :to="{ path: '/catgConfig' }">品类配置</el-breadcrumb-item>
                <el-breadcrumb-item>创建品类</el-breadcrumb-item>
            </el-breadcrumb>
            <!-- form  -->
            <el-row>
                <el-col :span="14" :offset="4">
                    <div class="title m-b-25">创建品类</div>

                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="130px">

                        <el-row>
                            <el-col :span="8">
                                <el-form-item label="品类名称" prop="catgname" required>
                                    <el-input type="text" v-model="ruleForm.catgname"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="品类编码" prop="catgcode" required>
                                    <el-input type="text" v-model="ruleForm.catgcode"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="查询类别" prop="queryCategory" required>
                                    <el-select v-model="ruleForm.queryCategory" placeholder="请选择">
                                        <el-option label="通用品类" value="0"></el-option>
                                        <el-option label="服务品类" value="1"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-form-item label="查询可选条件" prop="queryCondition" required>
                            <el-input type="textarea" v-model="ruleForm.queryCondition"></el-input>
                        </el-form-item>

                        <el-form-item label="启用状态" prop="initiateState" required>
                            <el-radio-group v-model="ruleForm.initiateState">
                                <el-radio label="0">启用</el-radio>
                                <el-radio label="1">禁用</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="对应车型属性" prop="category_cmpros_add" required>
                            <el-row>
                                <el-col :span="12">
                                    <el-card class="m-10">
                                        <div slot="header" class="card-title">
                                            <span>待选列表</span>
                                        </div>
                                        <draggable class="card-list list-group"
                                        @end="onEnd"
                                        v-model="ruleForm.category_cmpros"
                                        :options="dragOptions">
                                            <transition-group tag="ul" type="transition" name="flip-list" >
                                                <li class="list-group-item" v-for="row in ruleForm.category_cmpros" :key="row">
                                                    <el-tag type="gray">{{row.category_cmpro_index}}</el-tag> {{row.carmodel_pro_name}}
                                                </li>
                                            </transition-group>
                                        </draggable>
                                    </el-card>
                                </el-col>
                                <el-col :span="12">
                                    <el-card class="m-10">
                                        <div slot="header" class="card-title">
                                            <span class="fr f12">是否必须</span>
                                            <span>已选列表</span>
                                        </div>
                                         <draggable element="ul" class="card-list"
                                        @end="onEnd"
                                        v-model="ruleForm.category_cmpros_add"
                                        :options="dragOptions">
                                            <transition-group class="list-group" type="transition" name="flip-list" tag="ul">
                                                <li class="list-group-item" v-for="row in ruleForm.category_cmpros_add" :key="row">
                                                    <el-tag type="gray">{{row.category_cmpro_index}}</el-tag>
                                                    {{row.carmodel_pro_name}}
                                                    <el-checkbox class="fr" v-model="row.category_cmpro_required" :true-label="1"  :false-label="0"></el-checkbox>
                                                </li>
                                            </transition-group>
                                        </draggable> 
                                    </el-card>
                                </el-col>
                            </el-row>
                        </el-form-item>

                        <div class="title m-b-25">创建产品</div>
                        <div v-for="(part, index) in ruleForm.parts" :key="index">
                            <el-row>

                                <el-col :span="7">
                                    <el-form-item :label="'产品名称' + index" :key="part.name" :prop="'parts.'+index + '.name'" :rules="{required: true, message: '产品名称不能为空', trigger: 'blur'}">
                                        <el-input v-model="part.name"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="7">
                                    <el-form-item :label="'产品编码' + index" :key="part.code" :prop="'parts.'+index + '.code'" :rules="{required: true, message: '产品编码不能为空', trigger: 'blur'}">
                                        <el-input v-model="part.code"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="7">
                                    <el-form-item :label="'产品序号' + index" :key="part.serial" :prop="'parts.'+index + '.serial'" :rules="{required: true, message: '产品序号不能为空', trigger: 'blur'}">
                                        <el-input v-model="part.serial"></el-input>
                                    </el-form-item>
                                </el-col>

                                <el-col :span="3">
                                    <el-button class="m-l-10" icon="delete" @click.prevent="removePart(item)">删除</el-button>
                                </el-col>
                            </el-row>
                        </div>

                        <el-form-item>
                            <el-button icon="plus" @click.prevent="addPart">新增产品</el-button>
                        </el-form-item>

                        <el-form-item>
                            <el-button class="input-width" type="primary" @click="submitForm('ruleForm')"> 创建品类 </el-button>
                        </el-form-item>

                    </el-form>

                </el-col>
            </el-row>

        </div>
    </div>
</template>

<script>
import pageSet from '@/components/main/js/catgAdd';

export default {
    name: 'catgAdd',
    components: pageSet.components,
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";
.input-width {
    width: 30%;
}

.title {
    font-weight: bold;
    font-size: 18px;
}

.card-title {
    font-weight: bold;
    font-size: 15px;
}

.card-list{
    max-height: 200px;
    overflow-y: auto;
}

.list-group {
    min-height: 200px;
}

.ghost {
    opacity: .5;
    background: $color-D;
}

.flip-list-move {
    transition: transform 0.5s;
}

.list-group-item {
    padding: 0 10px;
    cursor: move;
}

.list-group-item i {
    cursor: pointer;
}
</style>

<style lang="scss">
@import "../../assets/css/config.scss";
.catgAdd {
    .el-card__body,
    .el-card__header {
        padding: 5px 10px;
    }
}
</style>



